.catalog__item {
  margin: 0 -34px -40px;
  text-align: center;
  border: 1px solid rgba(255,255,255,0);
  border-radius: 5px;
  padding: 14px 24px;
  position: relative;
  &:hover {
    border-color: $colorGrey;
    z-index: 1;
    -webkit-transition: all 1s ease-in-out;
    -o-transition: all 1s ease-in-out;
    transition: all 1s ease-in-out;
    .btn-catalog {
      opacity: 1;
      -webkit-transition: all .6s ease-in-out;
      -o-transition: all .6s ease-in-out;
      transition: all .6s ease-in-out;
    }
    .catalog__item-size {
      opacity: .6;
      -webkit-transition: all .6s ease-in-out;
      -o-transition: all .6s ease-in-out;
      transition: all .6s ease-in-out;
    }
    .catalog__item-size_active {
      opacity: 1;
      -webkit-transition: all .6s ease-in-out;
      -o-transition: all .6s ease-in-out;
      transition: all .6s ease-in-out;
    }
    .choose-color {
      opacity: 1;
      -webkit-transition: all .6s ease-in-out;
      -o-transition: all .6s ease-in-out;
      transition: all .6s ease-in-out;
    }
  }
}
.btn-catalog {
  opacity: 0;
}
.catalog__item-size {
  color: #025929;
  opacity: 0;
  text-align: left;
  cursor: pointer;
}

.catalog__item-size_active .catalog__item-size-name {
  font-family: "sfB";
}
.catalog__item-size-sm {
  font-size: 11px;
}