@import '__item/navbar-nav__item.scss';
@import '__link/navbar-nav__link.scss';

.navbar-nav {
  text-align: justify;
  line-height: 0;
  font-size: 1px;
  text-justify: newspaper;
  zoom: 1;
  text-align-last: justify;
  text-transform: uppercase;
  padding: 15px 0;
  &:after {
    width: 100%;
    height: 0px;
    visibility: hidden;
    overflow: hidden;
    content: '';
    display: inline-block;
  }
}