.choose-color__item {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: block;
  margin-bottom: 14px;
}
.choose-color__item_color1 {
  background: $white;
  border: 1px solid $colorGrey;
}
.choose-color__item_color2 {
  background: $colorYellow;
}
.choose-color__item_color3 {
  background: $colorTomat;
}
.choose-color__item_color4 {
  background: $colorPink;
}