@import '__input/awesomplete__input.scss';
.awesomplete {
  ul {
    border: solid 1px $colorGrey;
    li {
      font-size: 14px;
      background-color: $white;
      color: #000;
      padding: 10px;
      cursor: pointer;
      &:hover {
        background-color: $colorLightGreen;
        mark {
          background-color: $colorLightGreen;
        }
      }
      mark {
        background-color: $white;
      }
    }
    background-color: $white;
    
  }
}