// @include font-face('sfM', '../fonts/SFUIText/SFUIText-Medium', true, 'sans-serif');
// @include font-face('sfL', '../fonts/SFUIText/SFUIText-Light', true, 'sans-serif');
// @include font-face('Charter', '../fonts/Charter/Charter', true, 'serif');
// @include font-face('HelveticaNeue', '../fonts/HelveticaNeue/HelveticaNeue', true, 'sans-serif');
// @include font-face('TimesNewRoman', '../fonts/TimesNewRoman/TimesNewRoman', true, 'sans-serif');
@include font-face(sf, "../../fonts/SFUIText/SFUIText-Regular", normal, normal, otf);
@include font-face(sfB, "../../fonts/SFUIText/SFUIText-Bold", normal, normal, otf);
@include font-face(sfM, "../../fonts/SFUIText/SFUIText-Medium", normal, normal, otf);
@include font-face(sfL, "../../fonts/SFUIText/SFUIText-Light", normal, normal, otf);
@include font-face(Charter, "../../fonts/Charter/Charter", normal, normal, eot svg woff ttf);
@include font-face(HelveticaNeue, "../../fonts/HelveticaNeue/HelveticaNeue", normal, normal, eot svg woff ttf);
@include font-face(TimesNewRoman, "../../fonts/TimesNewRoman/TimesNewRoman", normal, normal, eot svg woff ttf);

$baseFontSize: 16px !default;
$ieSprite: '.lt-ie9' !default;
$sprite: map-get($icons, sprite) !default;

$white: #fff;
$colorYellow: #f8eb33;
$colorTomat: #e17474;
$colorDark: #3a3a3a;
$colorGrey: #e6e6e6;
$colorMiddleGrey: #b7b7b7;
$colorDarkGrey: #949a96;
$colorPink: #e395c0;
$colorPinkLight: #f49ac1;
$colorPinkHover: #dc7ab0;
$colorPinkDeep: #ff92ce;
$colorPaleGreen: #c1d0c7;
$colorLightGreen: #e8f1ed;
$colorGreen: #698e7d;
$colorDarkGreen: #006134;
$lightPurple: #d4d3ff;



* {
  box-sizing: border-box;
}
html, body {
  padding: 0;
  margin: 0;
}
body {
  background-color: #ffffff;
  font-family: "sf";
  font-size: 14px;
  line-height: 1.3;
  color: #3a3a3a;
}
article,aside,figcaption,figure,footer,header,nav,section {
  display: block;
}

a {
  text-decoration: none;
}
ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, .h1 {
  font-size: 36px;
  text-transform: uppercase;
  font-family: Charter;
  font-weight: normal;
}
h2, .h2 {
  font-size: 36px;
  font-family: Charter;
  font-weight: normal;
}
h3, .h3 {
  font-size: 18px;
  font-family: "sfB"; 
}
p {
  font-size: 15px;
  margin: 0 0 10px;
}
.subtitle {
  font-size: 24px;
  font-family: 'Times New Roman';
  font-style: italic;
  padding: 15px 0 45px;
  text-align: center;
}

.container {
  width: 1024px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
.hidden {
  display: none;
}
.pull-left {
  float: left;
}
.pull-right {
  float: right;
}
.text-left {
  text-align: left;
}
.text-center {
  text-align: center;
}
.text-right {
  text-align: right;
}
.text-jst {
  text-align: justify;
}
.in-bl {
  display: inline-block;
}
.prel {
  position: relative;
}
.clearfix:before, .clearfix:after,
.row:before, .row:after {
  content: '';
  display: table;
  clear: both;
}
.row {
  margin: 0 -12px;
}

.col {
  float: left;
  padding: 0 12px;
}
.col-1 {
  width: 8.33333333%;
}
.col-2 {
  width: 16.66666667%;
}
.col-3 {
  width: 25%;
}
.col-4 {
  width: 33.33333333%;
}
.col-5 {
  width: 41.66666667%;
}
.col-6 {
  width: 50%;
}
.col-7 {
  width: 58.33333333%;
}
.col-8 {
  width: 66.66666667%;
}
.col-9 {
  width: 75%;
}
.col-10 {
  width: 83.33333333%;
}
.col-11 {
  width: 83.33333333%;
}
.col-12 {
  width: 100%;
}

.btn {
  font-size: 16px;
  line-height: 30px;
  color: $colorPink;
  border: 1px solid $colorPink;
  padding: 5px 20px;
  min-width: 150px;
  border-radius: 5px;
  cursor: pointer;
  display: inline-block;
  &:hover {
    color: $colorPinkHover;
    border-color: $colorPinkHover;
  }
}
.btn-bg {
  font-size: 18px;
  background-color: #e395c0;
  color: $white;
  &:hover {
    color: $white;
    background-color: #dc7ab0;
  }
}
.btn_main-banner {
  position: absolute;
  right: 0;
  bottom: 0;
  text-transform: uppercase;
  border-radius: 10px 10px 0 0;
  font-size: 13px;
  font-family: "sfM"; 
  padding-left: 15px;
  padding-right: 15px;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,.11);
}

.link {
  cursor: pointer;
  text-decoration: none;
}
.link_underline {
  text-decoration: underline;
}
.link_size_s {
  font-family: "sfL"; 
  font-size: 12px;
}
.link_more {
  color: #ff92ce;
  border-bottom: 1px dashed;
}
.line {
  height: 1px;
  background: url(../images/line.png) center no-repeat;
  margin: 65px 0 85px;
}
