.dropdown__content {
  display: none;
  position: absolute;
  background-color: $white;
  border-radius: 5px;
  border: solid 1px $colorGrey;
  z-index: 10;
  left: 0;
  top: 26px;
  z-index: 10;
  padding: 25px;
  // width: 450px;
  &:after{
    z-index: -1;
    width: 14px;
    height: 14px;
    border-top: solid 1px $colorGrey;
    border-left: solid 1px $colorGrey;
    background-color: #fff;
    content: '';
    position: absolute;
    top: -7px;
    -webkit-transform: rotate(45deg);
    -ms-transform: rotate(45deg);
    -o-transform: rotate(45deg);
    transform: rotate(45deg);
  }
}